// Search Functionality for Courses
/* eslint-disable no-use-before-define */
/* global bootstrap */
document.addEventListener('DOMContentLoaded', function() {
    const searchModal = document.getElementById('searchModal');
    const searchInput = searchModal ? searchModal.querySelector('input[type="text"]') : null;
    const searchButton = searchModal ? searchModal.querySelector('button[type="submit"]') : null;
    const topSearchInput = document.querySelector('#formGroupExampleInput');
    
    // Course data (in real app, this would come from API)
    const courses = [
        {
            title: 'Introduction to Algebra',
            category: 'mathematics',
            gradeLevel: 'middle',
            description: 'Learn the fundamentals of algebraic thinking',
            price: 'free',
            rating: 4.8
        },
        {
            title: 'Biology: Life Sciences',
            category: 'science',
            gradeLevel: 'high',
            description: 'Explore the fascinating world of living organisms',
            price: 'paid',
            rating: 4.6
        },
        {
            title: 'Digital Art Fundamentals',
            category: 'arts',
            gradeLevel: 'high',
            description: 'Master the basics of digital illustration',
            price: 'free',
            rating: 4.9
        },
        {
            title: 'Creative Writing Workshop',
            category: 'language',
            gradeLevel: 'high',
            description: 'Develop your writing skills through exercises',
            price: 'free',
            rating: 4.7
        },
        {
            title: 'Introduction to Coding',
            category: 'technology',
            gradeLevel: 'middle',
            description: 'Learn programming fundamentals with projects',
            price: 'paid',
            rating: 4.9
        }
    ];
    
    // Search functionality
    if (searchButton) {
        searchButton.addEventListener('click', performSearch);
    }
    
    if (searchInput) {
        searchInput.addEventListener('keypress', function(e) {
            if (e.key === 'Enter') {
                performSearch();
            }
        });
    }
    
    if (topSearchInput) {
        topSearchInput.addEventListener('keypress', function(e) {
            if (e.key === 'Enter') {
                performTopSearch();
            }
        });
    }
    
    function performSearch() {
        const query = searchInput.value.trim().toLowerCase();
        if (!query) return;
        
        const results = searchCourses(query);
        displaySearchResults(results, query);
        
        // Close modal and redirect to courses page with results
        if (searchModal) {
            const modal = bootstrap.Modal.getInstance(searchModal);
            if (modal) modal.hide();
        }
        
        // Redirect to courses page with search parameter
        window.location.href = `courses.html?search=${encodeURIComponent(query)}`;
    }
    
    function performTopSearch() {
        const query = topSearchInput.value.trim().toLowerCase();
        if (!query) return;
        
        window.location.href = `courses.html?search=${encodeURIComponent(query)}`;
    }
    
    function searchCourses(query) {
        return courses.filter(course => 
            course.title.toLowerCase().includes(query) ||
            course.description.toLowerCase().includes(query) ||
            course.category.toLowerCase().includes(query)
        );
    }
    
    function displaySearchResults(results, query) {
        console.log(`Search results for "${query}":`, results);
        // In a real implementation, this would update the UI
    }
    
    // Popular search tags
    const popularTags = document.querySelectorAll('.badge.bg-light');
    popularTags.forEach(tag => {
        tag.addEventListener('click', function handleTagClick() {
            const searchTerm = this.textContent.trim();
            if (searchInput) {
                searchInput.value = searchTerm;
                performSearch();
            }
        });
    });
    
    // Category badges in search modal
    const categoryBadges = document.querySelectorAll('.badge.bg-primary, .badge.bg-info, .badge.bg-danger, .badge.bg-success, .badge.bg-dark');
    categoryBadges.forEach(badge => {
        badge.addEventListener('click', function handleBadgeClick(e) {
            e.preventDefault();
            const category = this.textContent.trim();
            if (searchInput) {
                searchInput.value = category;
                performSearch();
            }
        });
    });
    
    // URL parameter handling for search
    function handleSearchParameters() {
        const urlParams = new URLSearchParams(window.location.search);
        const searchQuery = urlParams.get('search');
        const category = urlParams.get('category');
        const grade = urlParams.get('grade');
        
        if (searchQuery && searchInput) {
            searchInput.value = searchQuery;
            performSearch();
        }
        
        if (category) {
            // Auto-select category filter
            const categoryCheckbox = document.getElementById(`cat-${category.toLowerCase()}`);
            if (categoryCheckbox) {
                categoryCheckbox.checked = true;
                // Trigger filter update
                const event = new Event('change');
                categoryCheckbox.dispatchEvent(event);
            }
        }
        
        if (grade) {
            // Auto-select grade filter
            const gradeCheckbox = document.getElementById(`grade-${grade.toLowerCase()}`);
            if (gradeCheckbox) {
                gradeCheckbox.checked = true;
                // Trigger filter update
                const event = new Event('change');
                gradeCheckbox.dispatchEvent(event);
            }
        }
    }
    
    // Initialize search parameters if on courses page
    if (window.location.pathname.includes('courses.html')) {
        handleSearchParameters();
    }
    
    // Auto-suggestions (simplified version)
    if (searchInput) {
        searchInput.addEventListener('input', function handleSearchInput() {
            const query = this.value.trim().toLowerCase();
            if (query.length > 2) {
                // In a real implementation, this would show a dropdown with suggestions
                console.log('Auto-suggestions for:', query);
            }
        });
    }
});
